<template>
  <div class="l-pigList">
    <van-nav-bar 
      title="猪猪商城"
      left-arrow 
      @click-left="onClickLeft" 
      class="c-nav-bar"
    >
    </van-nav-bar>
    <ul class="l-body c-content">
      <li v-for="item in goodList" class="l-list" :key="item.goodsid">
        <div class="l-img"><img :src="item.pic_url" @error.once="setDefImg"/></div>
        <div class="l-imgInfo">
          <div class="l-img-title">
            <span>{{item.name}}</span>
            <span class="l-level"><img :src="require('../../assets/icon/level' + item.goodsid +'.jpg')"></span>
          </div>
          <div class="l-img-price">
            <span>价格:</span><span class="l-img-marginL">{{item.price}}</span><span>xtoken</span>
          </div>
          <div class="l-img-memo">
            <span>产能:</span><span class="l-img-marginL">{{item.memo}}</span>
          </div>
        </div>
        <div class="l-num">
          <span v-show="item.count > 0" @click="reduce(item)"><img :src="require('../../assets/icon/reduce.png')"></span>
          <span v-show="item.count > 0">{{item.count}}</span>
          <span @click="add(item)"><img :src="require('../../assets/icon/add.png')"></span>
        </div>
      </li>
    </ul>
    <div class="l-list-footer">
      <div class="l-footer-img"><img :src="require('../../assets/icon/coin.png')"></div>
      <div class="l-footer-right">
        <div class="l-footer-amount"><span>{{amount | toNumberString}}</span>xtoken</div>
        <div class="l-footer-balance" :class="{disabled: theLink==''}" ><router-link :to="theLink" >去购物车结算</router-link></div>
      </div>
    </div>
    <page-loading v-show="loading"></page-loading>
  </div>
</template>
<script>
import '../../common/common.scss'
import '../../common/shop.scss'
import util from '../../common/util'
export default {
  name: 'shop',
  data() {
    return {
      loading: false,
      errorImg: require('../../assets/pic/error-img.jpg'),
      goodList: [
        // {
        //   goodsid : 1,       //商品ID
        //   name :  "佩奇",  //商品名字
        //   price :  500,      //商品价格
        //   memo :  "describtiodescribtiondescribtiondescribtiondescribtiondescribtiondescribtiondescribtiondescribtiondescribtionn",  //商品详细描述
        //   pic_url : require('../../assets/pic/pq.jpg'), //商品图片url
        //   level: require('../../assets/icon/level1.jpg'), //商品图片url
        //   count: 2,
        // },
        // {
        //   goodsid : 2,       //商品ID
        //   name :  "伊比利亚",  //商品名字
        //   price :  200,      //商品价格
        //   memo :  "describtion",  //商品详细描述
        //   pic_url : require('../../assets/pic/ybly.jpg'), //商品图片url
        //   level: require('../../assets/icon/level2.jpg'), //商品图片url
        //   count: 2,
        // },
        // {
        //   goodsid : 3,       //商品ID
        //   name :  "巴克夏猪",  //商品名字
        //   price :  300,      //商品价格
        //   memo :  "describtion",  //商品详细描述
        //   pic_url : require('../../assets/pic/bkxz.jpg'), //商品图片url
        //   level: require('../../assets/icon/level3.jpg'), //商品图片url
        //   count: 2,
        // },
        // {
        //   goodsid : 4,       //商品ID
        //   name :  "红山草猪",  //商品名字
        //   price :  200,      //商品价格
        //   memo :  "describtion",  //商品详细描述
        //   pic_url : require('../../assets/pic/hscz.jpg'), //商品图片url
        //   level: require('../../assets/icon/level4.jpg'), //商品图片url
        //   count: 2,
        // }
      ]
    };
  },

  methods: {
    onClickLeft () {
      //console.log(this)
      this.$router.back();
    },
    setDefImg(e) {
      e.target.src = this.errorImg;
    },
    getData() {
      this.loading = true;
      Promise.all([this.getGoodList(), this.getCart()]).then((res)=> {
        let [goodList, cartList] = res;
        //console.log(goodList, cartList)
        this.setCartDict(cartList);
        this.setGoodList(goodList);   
        this.loading = false;       
      }).finally(() => {
        this.loading = false;
      }); 
    },
    setCartDict(list) {
      list = list || [];
      this.cartDict = list.reduce((pre, cur) => {
        pre[cur.goodsid] = cur;
        return pre;
      }, {})
    },
    setGoodList(list) {
      list = list || [];
      list.forEach((obj) => {
        obj.count = this.cartDict[obj.goodsid] ? this.cartDict[obj.goodsid].count : 0
      })
      this.goodList = list;
    },
    getGoodList() {
      return this.$http.post("/shop/getgoodslist", {
        "appid":11,   //商品ID  
        "goods_type_id": 1//商品类型id
      });   
    },
    getCart() {
      return this.$http.post("/order/getCart", {
        "goods_type_id": 1
      })  
    },
    add (item) {
      this.loading = true;
      this.$http.post("order/increaseCart", {
        "goodsid": item.goodsid
      }).then((res) => {
        item.count++;
      }).catch((e) => {
        let code = util.getErrorCode(e);
        if(code == window.globalData.code.CART_COUNT_IS_BEYOND_LIMIT) {
          this.$dialog.alert({
            message: '购买数量超出限制'
          });
        }
      }).finally(() => {
        this.loading = false;
      });      
    },
    reduce (item) {
      this.loading = true;
      this.$http.post("order/reduceCart", {
        "goodsid": item.goodsid
      }).then((res) => {
        item.count--;
      }).catch((e) => {
        console.log(e)
      }).finally(() => {
        this.loading = false;
      });        
    },
    getTotal() {
      return this.goodList.reduce((pre, next) => {
        return pre + next.price*next.count
      },0)
    }
  },
  mounted () {
    this.getData();
  },
  computed: {
    amount() {
      let amountAll = this.getTotal();
      //let amountNew = amountAll.toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
      return amountAll;
    },
    theLink() {
      if(parseFloat(this.amount) > 0) {
        return "/shop/cart";
      }
      else {
        return "";
      }
    }
  }
}
</script>
